<script>
// import imgtest from "@/Pages/Common/imgtes"
import {
  pickingsdetail,
  Carriers,
  WayParterdata,
  GetInputID,
  GetBoxesdates,
  Boxenter,
  pickingsenter,
  GetBoxes
} from "../../api";
import {CgPickingsDetail,CgpickingsoutBtn,CgPickingsSearch} from '../../api/CgApi'
import moment from "moment";
let lodash = require("lodash");
import {
  TemplatePrint,
  GetPrinterDictionary,
  FilePrint,
  FormPhoto,
  SeletUploadFile
} from "@/js/browser.js";
export default {
  name: "",
  components: {
    //  'img-test':imgtest
  },
  data() {
    return {
      SelectRow: [], //多选数组
      searchkey: "",
      boxingarr: [ ], //箱号
      printlist: [], //清单打印数据
      wayBillID: this.$route.params.detailID,
      detail_ID: "",
      showphoto: false, //显示拍照弹出框
      CarrierList: [], //承运商列表
      Conveyingplace: [], //输送地列表
      waybillinfo: {},//运单信息
      detailitem:[],//通知信息
      details: {
        //详情页
        wayBillID: "",
        total: 0,
        pageIndex: 1,
        pageSize: 1,
        WaybillNo: "90416165067", //运单号(本次)
        Carrier: "", //承运商(本次)
        PlaceDescription: "美国" //输送地,
      },
      tabletitle: [
        {
          type: "selection",
          width: 50,
          align: "center"
        },
        {
          title: " ",
          slot: "indexs",
          align: "left",
          width: 30
          // fixed: 'right'
        },
        {
          title: "型号",
          slot: "PartNumber",
          align: "center",
          width: 180
        },
        {
          title: "品牌",
          key: "brand",
          align: "center",
          render: (h, params) => {
            var vm = this;
            return h("span", {}, params.row.Product.Manufacturer);
          }
        },
        {
          title: "批次",
          key: "batch",
          align: "center",
          render: (h, params) => {
            var vm = this;
            return h("span", {}, params.row.DateCode);
          }
        },
        {
          title: "库位",
          key: "ShelveID",
          align: "center",
          render: (h, params) => {
            var vm = this;
            return h("span", {}, params.row.ShelveID);
          }
        },
        {
          title: "应发数量",
          key: "Quantity",
          align: "center",
          render: (h, params) => {
            var vm = this;
            return h("span", {}, params.row.Quantity);
          }
        },
        {
          title: "已发数量",
          key: "PickedQuantity",
          align: "center",
